import React, { useRef } from 'react'
import { css } from '@emotion/core'

import { useIntersection } from 'use-intersection'

let difference = 9999
let entryTargetId = null
let intersectionRectY = 0
let isPreviousElement = false

const options = {
	root: null,
	rootMargin: '-90% 0px',
}

const callback = entry => {
	const target = entry.target
	const currentY = entry.boundingClientRect.y

	const currentDifference = Math.abs(0 - currentY)
	if (currentDifference < difference) {
		entryTargetId = target.id
		difference = currentDifference
	}

	if (entry.isIntersecting) {
		if (entry.intersectionRect.y !== 0) {
			intersectionRectY = entry.intersectionRect.y
			entryTargetId = target.id
		}
	} else {
		if (intersectionRectY !== 0 && currentY > intersectionRectY) {
			isPreviousElement = true
		}
	}

	if (entryTargetId || isPreviousElement) {
		const tocElements = document.querySelectorAll('.article-toc')
		for (const tocElement of tocElements) {
			const linkElements = tocElement.querySelectorAll('a')
			for (const linkElement of linkElements) {
				linkElement.classList.remove('bg-gray-100')
			}
			if (isPreviousElement) {
				let previousElement = null
				for (const linkElement of linkElements) {
					if (
						linkElement.getAttribute('data-id') === target.id &&
						previousElement !== null
					) {
						previousElement.classList.add('bg-gray-100')
						tocElement.dataset.active = previousElement.getAttribute('data-id')
						break
					}
					previousElement = linkElement
				}
			} else {
				tocElement
					.querySelector(`a[data-id="${entryTargetId}"]`)
					.classList.add('bg-gray-100')
				tocElement.dataset.active = entryTargetId
			}
			entryTargetId = null
			isPreviousElement = false
			return
		}
		entryTargetId = null
		isPreviousElement = false
	}
}

const H1 = props => {
	const target = useRef(null)
	useIntersection(target, options, callback)

	return (
		<h1
			ref={target}
			className="text-6xl font-bold leading-12 mt-8 mb-8"
			{...props}
		/>
	)
}

const H2 = props => {
	const target = useRef(null)
	useIntersection(target, options, callback)

	return (
		<h2
			ref={target}
			className="text-4xl font-semibold leading-8 mt-8 mb-4"
			{...props}
		/>
	)
}

const H3 = props => {
	const target = useRef(null)
	useIntersection(target, options, callback)

	return (
		<h3
			ref={target}
			className="text-2xl font-bold mt-6 mb-4 px-4 py-2"
			css={css`
				--bg-opacity: 1;
				background-color: #f4f5f7;
				background-color: rgba(244, 245, 247, var(--bg-opacity));
				border-left: 4px solid #333;
			`}
			{...props}
		/>
	)
}

const H4 = props => {
	const target = useRef(null)
	useIntersection(target, options, callback)

	return (
		<h4
			ref={target}
			className="relative text-xl font-medium mt-4 mb-2 py-2"
			css={css`
				border-bottom: 2px solid #e5e7eb;
				&:after {
					background: #333;
					bottom: -2px;
					content: '';
					height: 2px;
					left: 0;
					position: absolute;
					width: 10%;
				}
			`}
			{...props}
		/>
	)
}

const H5 = props => {
	const target = useRef(null)
	useIntersection(target, options, callback)

	return (
		<h5 ref={target} className="text-xl font-medium mt-4 mb-2" {...props} />
	)
}

const H6 = props => {
	const target = useRef(null)
	useIntersection(target, options, callback)

	return (
		<h6 ref={target} className="text-xl font-medium mt-4 mb-2" {...props} />
	)
}

export { H1, H2, H3, H4, H5, H6 }
